<template>
    <transition name="router-order" mode="out-in">
        <div class="my-zone">
            <head-top head-title="myZone" show-logout = "mrZone"></head-top>
            <div class="my-zone-content">
                <h1>{{test}}</h1>
                <mt-button size="small" style="min-width:60px;margin-left: 20%" type="primary" @click="goRouter()"></mt-button>
                <mt-button size="small" style="min-width:60px;margin-left: 20%" type="primary" @click="goUploader()"></mt-button>
                <mt-button size="small" style="min-width:60px;margin-left: 20%;margin-top: 20px;" type="primary" @click="goDrag()"></mt-button>
            </div>
        </div>
    </transition>
</template>

<script>
    import headTop from './../common/header'
    export default {
        name: 'hello',
        data () {
            return {
                msg: 'Welcome to Your Vue.js App',
                test:"myzone",
            }
        },
        components: {
            headTop,
        },
        //<keep-alive> created,mounted
        created: function () {
            console.log(1)
        },
        mounted: function () {
            console.log(2)
        },
        activated: function () {
            console.log(3);
        },
        deactivated: function () {
            console.log(4)
        },
        // 
        beforeRouteEnter (to, from, next) {
            if(true){
                console.log("");
                next();
            }else{
                console.log("");
                next(false)
            }
        },
        methods:{
            goRouter(){
                let that = this;
                that.$router.push('/rotation')
            },
            goUploader(){
                let that = this;
                that.$router.push('/uploader')
            },
            goDrag(){
                let that = this;
                that.$router.push('/drag')
            }
        }
    }
</script>

<style lang="less" type="text/css" scoped>
    .my-zone{
        background: orange;
        height:300px;
    }
    .my-zone-content{
        overflow: hidden;
        margin-top: 44px;
        width:100%;
    }
    .router-order-enter-active, .router-order-leave-active {
        transition: background 3s;
    }

    .router-order-enter, .router-order-leave-active {
        background: #fff;
    }
</style>



