<template>
    <div class="my-zone">
        <head-top head-title="" back="drag" show-logout = "ratation"></head-top>
        <div class="my-zone-content">
            <div class="swiper-container">
                <div class="swiper-wrapper">
                    <div class="swiper-slide"><img src="../../../../images/muwu.jpg"></div>
                    <div class="swiper-slide"><img src="../../../../images/shuijiao.jpg"></div>
                    <div class="swiper-slide"><img src="../../../../images/yuantiao.jpg"></div>
                </div>
                <div class="swiper-button-prev"></div>
                <div class="swiper-button-next"></div>
                <div class="swiper-pagination"></div>
            </div>
        </div>
    </div>
</template>

<script>
    //swiper  ::npm install --save swiper,:,:
    import headTop from './../../common/header'
    import '../../../../plugins/css/swiper.min.css'
    import Swiper from 'swiper';

    export default {
        name: 'hello',
        data () {
            return {

            }
        },
        components: {
            headTop,
        },
        //<keep-alive> created,mounted
        created: function () {
            console.log(1)
        },
        mounted: function () {
            //swiper
            this.$nextTick(function () {
                var swiper = new Swiper('.swiper-container', {
                    prevButton:'.swiper-button-prev',
                    nextButton:'.swiper-button-next',
                    pagination: '.swiper-pagination',
                    paginationClickable: true,
                    loop: true,
                    grabCursor:true,//trueSwiperpc
                    autoplayDisableOnInteraction : false,//swiperautoplaytrue
                    speed: 600,//sliderms
                    autoplay: 3000,//
//                    initialSlide :2,//slide
                    direction : 'horizontal',//Slides(horizontal)(vertical)
                    onTouchEnd: function() {
                        swiper.startAutoplay()
                    }
                });
            })

            console.log(2)
        },
        activated: function () {
            console.log(3);
        },
        deactivated: function () {
            console.log(4)
        },
        methods:{

        }
    }
</script>

<style lang="less" type="text/css" scoped>

    .my-zone{
        background: orange;
        height:300px;
    }
    .my-zone-content{
        overflow: hidden;
        margin-top: 44px;
        width:100%;
    }
    .swiper-container{
        width: 100%;
        height: 300px;
        .swiper-wrapper{
            width: 100%;
            height: 100%;
        }
        .swiper-slide{
            background-position: center;
            background-size: cover;
            width: 100%;
            height: 300px;
            img{
                display: block;
                height: 300px;
                width:100%;
            }
        }
    }
</style>



